.home{
    margin: 0px 20px 25px;
    &__banner{
        padding: 75px;
        text-align: center;
        background: $dark;
        color: $light;
        margin: 20px 0px;
        border-radius: 12px;
    }
    &__explore, &__guide{
        display: flex;
        flex-direction: column;
        &__title{
            @include headline-style;
        }
        &__explanation{
            @include paragraph-style;
            padding-right: 5px;
        }
    }
    &__carousel {
        display: flex;
        overflow-x: scroll;
        padding-left: 75px;
        scroll-snap-type: x mandatory;
        scroll-padding: 0px;
        gap: 12px;
        margin: 8px 0px 15px;
        border-radius: 8px;
        position: relative;
        z-index: -1;
        &::-webkit-scrollbar{
            display: none;
        }
        &__card{
            flex: 0 0 100%;
            height: 180px;
            border-radius: 8px;
            scroll-snap-align: center;
            background-size: cover;
            background-position: center;
            position: relative;
            &:first-child{
                scroll-snap-align: start;
            }
            &::before{
                content: "";
                position: absolute;
                background-color: $darkShadow;
                height: 100%;
                width: 100%;
            }
        }
    }
    &__tags{
        margin: 10px 0px;
        &__list{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-bottom: 15px;
            &__item{
                display: flex;
                justify-content: center;
                padding: 5px;
                border: solid 2px;
                border-right: none;
                border-color: $medium;
                width: 100%;
                font-size: 16px;
                font-weight: 500;
                color: $dark;
                &:first-child{
                border-right:solid 2px $medium ;
                }
            &:has(> input:checked){
                background-color: $medium;
                color: $light;
            }
            }

        }
    }
    &__result {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 10px;
        &__card {
            box-shadow: 0 2px 6px 0 $darkShadow;
            border-radius: 10px 10px 0px 0px;
            overflow: hidden;
            &__img{
                height: 20vh;
                background-size: cover;
                background-position: center;
                position: relative;
                z-index: -1;
                &::before{
                    position: absolute;
                    content: "";
                    background-color: $dark;
                    width: 100%;
                    height: 100%;
                    opacity: 0.4;
                }
            }
            &__container {
                display: flex;
                width: 100%;
                position: relative;
                z-index: -1;
                p{
                    font-weight: 600;
                    font-size: 14px;
                    color: $dark;
                    opacity: 0.6;
                    padding: 0px 10px 0px 10px;
                }
                &::before{
                    content: "";
                    width: 100%;
                    height: 2px;
                    position: absolute;
                    top: 0;
                    background-color: $dark;
                    opacity: 0.4;


                }
            }
        }
    }
}