html{
    scroll-behavior: smooth;
}
body{
    margin: 0;
    padding: 0;
    background: $light;
    font-family: 'Tajawal';
    user-select: none;
}
*{
    box-sizing: border-box;
}
ul {
    margin-block: 0;
    list-style: none;
    margin-inline: 0;
    padding: 0;
    display: inline;
}
li {
    list-style: none;
    display: inline;
}

a {
    color: inherit;
    text-decoration: none;
    cursor: pointer;
}